@keyframes slideInFromLeft {
    0% {
        transform: translateX(-150%); }
    100% {
        transform: translateX(0); } }
@keyframes gradient {
	0% {
		background-position: 0% 50%; }
	50% {
		background-position: 100% 50%; }
	100% {
		background-position: 0% 50%; } }
body {
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    padding: 0 8vw;
    color: $text95;
    @include bp-small {
        padding: 0 18px; } }
header {
    width: 60%;
    @include bp-small {
        width: 100%; } }
hr {
    background: linear-gradient(90deg, $secondary, $accent);
    height: 2px;
    width: 100%;
    border: 0;
    animation: gradient 10s ease infinite;
    background-size: 200% 200%; }
nav {
    height: 5vh;
    width: 84vw;
    ul {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        padding-left: 0;
        @include bp-small {
            justify-content: space-between; }
        li {
            list-style: none;
            padding: 0 2vw;
            @include bp-small {
                padding: 0; }
            a {
                color: $text95;
                &:hover {
                    color: $accent; } } } } }

.case-studies {
    flex-direction: row;
    display: flex;
    @include bp-small {
        flex-direction: column; }
    .case-study {
        text-decoration: none;
        color: inherit;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 4rem;
        @include bp-small {
            padding: 2rem 0; } }
    a {
        text-decoration: none;
        color: inherit;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 4rem;
        transition: background .1s ease-in-out;
        &:hover {
            background: $text02; } } }
.about {
    padding: 2rem 6rem;
    @include bp-small {
        padding: 1rem 0; } }
.contact {
    flex-direction: row;
    display: flex;
    @include bp-small {
        flex-direction: column; }
    .contact-form, .contact-list {
        flex: 1;
        display: flex;
        justify-content: top;
        align-items: center;
        flex-direction: column;
        padding: 4rem;
        @include bp-small {
            padding: 2rem 0; }
        ul {
            padding-left: 0;
            li {
                list-style: none;
                line-height: 3; } } } }
.images {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0;
    img {
        object-fit: cover;
        width: 100%; }
    p {
        font-size: 0.9rem;
        font-style: italic;
        margin-top: 0; } }
footer {
    color: $text35;
    padding: 5vh 8vw;
    text-align: center;
    a {
        white-space: pre; }
    @include bp-small {
        padding: 5vh 0; } }
