@mixin bp-large {
    @media only screen and (min-width: 80em) {
        @content; } }

@mixin bp-small {
    @media only screen and (max-width: 60em) {
        @content; } }

@mixin opacity($opacity) {
    opacity: $opacity;
    $opacity-ie: $opacity * 100;
    filter: alpha(opacity=$opacity-ie); } //IE8

@mixin transition($args...) {
    -webkit-transition: $args;
    -moz-transition: $args;
    -ms-transition: $args;
    -o-transition: $args;
    transition: $args; }

@mixin flex-row {
    display: flex;
    flex-direction: row; }

@mixin flex-column {
    display: flex;
    flex-direction: column; }

%clearfix {
    *zoom: 1;
    &:before, &:after {
        content: " ";
        display: table; }
    &:after {}
    clear: both; }

%visuallyhidden {
    margin: -1px;
    padding: 0;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    clip: rect(0, 0, 0, 0);
    position: absolute; }
