form {
    display: flex;
    flex-direction: column;
    width: 100%;
    label {
        color: $text65;
        font-size: 0.8rem; }
    input[type=text], input[type=email], textarea {
        font-family: 'Inter', sans-serif;
        color: $text35;
        border: none;
        border-bottom: 1px solid $text05;
        font-size: 1rem;
        line-height: 2rem;
        margin: 1rem;
        margin-left: 0;
        transition: all .2s ease-in-out; }
    input[type=text]:focus, input[type=email]:focus, textarea:focus {
        color: $text95;
        border: none;
        border-bottom: 1px solid $text65; }
    button {
        color: $bg;
        background: $secondary;
        background: linear-gradient(90deg, $secondary, $accent);
        font-size: 1rem;
        line-height: 1.5rem;
        padding: 0.75rem 1.15rem;
        border: none;
        border-radius: 3px;
        width: 40%;
        align-self: center;
        transition: background 2s ease-in-out;
        animation: gradient 10s ease infinite;
        background-size: 200% 200%;
        &:hover {
            background: linear-gradient(90deg, $accent, $accent);
            border: none;
            color: $bg;
            cursor: pointer; } } }
